.PersonalSkills{
  margin-left: 20%;
  margin-right: 5%;
  margin-bottom: 10%;
}
.skillsInnerFlex{
  display: flex;
  gap: 50px; 
  margin-left: 50px;
}
.skillsInnerFlex .title{
  font-size: 20px;
  font-weight: 500;
  color: #71eaa8;
}
.skillsFlex{
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.skillsInnerFlex div:nth-child(1){
  width: 50%;
  display: flex;
  flex-direction: column;
}
.title{
  font-size: 30px;
  margin-top: 10px;
}
.content{
  font-size: 18px;
}